export type AppDefId = string;
export type Listener = (payload: any) => void;
export type AnyListener = (payload: any, sourceEvent: string) => void;

export interface AssistantBmSdk {
  emitEvent(appDefId: AppDefId, event: string, payload: any): void;

  onEvent(appDefId: AppDefId, event: string, listener: Listener): void;

  offEvent(appDefId: AppDefId, event: string, listener: Listener): void;

  // sync with duplexer
  getActiveListeners(): { [appDefId: AppDefId]: string[] };

  setActiveListenersObserver(
    listener: (appDefId: AppDefId, added: string[]) => void,
  ): void;
}
